body {
  min-width: 500px;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  overflow-x: hidden;
}

.people {
  text-align: center;
  grid-template-rows: repeat(5, 231px);
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  align-items: center;
  gap: 40px 30px;
  min-width: 500px;
  padding: 30px 35px 0;
  display: grid;
}

.people__person {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 231px;
  padding: 0 10px;
  transition: scale 1s;
  display: flex;
}

.people__person:hover {
  z-index: 1;
  background-color: #eee;
  border-radius: 10px;
  height: 287px;
  scale: 1.2;
}

.people__person:hover .photo__source {
  border-radius: 60px;
}

.people__person:hover .details__extra {
  opacity: 1;
}

@media screen and (width >= 750px) {
  .people {
    grid-template-rows: repeat(5, 231px);
    grid-template-columns: repeat(2, 200px);
    padding: 30px 160px 0;
  }
}

@media screen and (width >= 1000px) {
  .people {
    grid-template-rows: repeat(3, 231px);
    grid-template-columns: repeat(4, 200px);
    padding: 30px 55px 0;
  }
}

@media screen and (width >= 1500px) {
  .people {
    grid-template-rows: repeat(3, 231px);
    grid-template-columns: repeat(4, 200px);
    padding: 30px 305px 0;
  }
}

.details__name {
  color: #2f4f4f;
  font-size: 20px;
}

.person__photo {
  position: relative;
}

.person__photo__source {
  box-sizing: border-box;
  border-radius: 200px;
  width: 200px;
  height: 200px;
  transition: border-radius 1s;
}

.person__photo__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  display: flex;
  position: absolute;
  top: 84px;
  right: 10px;
}

.details__extra {
  opacity: 0;
  font-size: 18px;
}

.details__extra__sport:after {
  content: ", ";
}

.details__extra__pay:before {
  content: "$";
}

.details__extra__pay:after {
  content: "M";
}

